.Characters {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.Character {
  flex-direction: column;
  flex-wrap: wrap;
  display: flex;
  border: 3px solid grey;
  border-radius: 5px;
  margin: 15px;
}

h2,
h3,
h4,
img {
  padding-bottom: 15px;
}

.FavList {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  font-size: 20px;
}

.Icon {
  height: 35px;
  width: 35px;
  cursor: pointer;
  padding-top: 10px;
}

.delete {
  height: 20px;
  width: 20px;
}
